* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #141024;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

p, ul {
  margin: 0;
  padding: 0;
}

.container {
  flex-direction: column;
  display: flex;
}

#search-box {
  min-width: 320px;
  font: inherit;
  border-radius: 2px;
  margin: 0 auto;
  padding: 8px;
  display: block;
}

#search-box, .country-list__item, .country__final-result {
  box-shadow: -2px 3px 5px #4f4c4f;
}

.country-list {
  min-width: 500px;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin: 10px auto 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.country-list__item {
  min-height: 17vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid #848484;
  border-radius: 2px;
}

.country-list__info-wrapper {
  width: 100%;
  max-width: 260px;
  height: 100%;
  color: inherit;
  background: linear-gradient(15deg, #ffffffe6 0%, #fff0 80%);
  align-items: flex-end;
  padding: 5px;
  text-decoration: none;
  transition: transform .3s;
  display: flex;
}

.country-list__info-wrapper:hover, .country-list__info-wrapper:focus {
  box-shadow: none;
  outline: none;
  transform: scale(1.03);
}

.country-list__name {
  margin: 0;
  font-weight: 600;
}

.country-info {
  flex-direction: column;
  margin: 0 auto;
  padding: 15px;
  display: flex;
}

.country__final-result--wrapper {
  padding: 20px 0;
  display: flex;
}

.country__final-result {
  max-width: 700px;
  max-height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 5px;
}

.country-info__wrapper {
  max-width: 600px;
  flex-direction: column;
  margin-left: 25px;
  padding-top: 35px;
  display: flex;
}

.country-info__name {
  color: #bcbde4;
  margin: 0;
  font-size: 42px;
  font-weight: 700;
  line-height: 1;
}

.country-info__categories {
  margin: 35px 0 0 25px;
  padding: 0;
  list-style: none;
}

.country-info__item:not(:first-child) {
  margin-top: 10px;
}

.country-info__category {
  color: #b3b1bc;
  font-weight: 600;
}

.country-info__category--value {
  color: #bcbde4;
  font-weight: 400;
}

